@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

html * {
  --main-text-color: #3d4e61;
  --box-shadow-color: #3d4e6133;
  --border-color: #e1e7ed;
  --text-shadow-color: #00000040;
  font-family: Avenir, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-form__input {
  box-sizing: border-box;
  width: 100%;
  margin-top: 20px;
  padding: 0 0 0 62px;
  display: block;
}

.search-form--big {
  border: 1px solid var(--border-color);
  box-shadow: 0 1px 8px 0 var(--box-shadow-color);
  background: url("Search.ccd3c07b.svg") 26px no-repeat;
  border-radius: 4px;
  height: 70px;
}

.search-form--big::placeholder {
  color: var(--main-text-color);
  line-height: 22px;
}

.search-form--big:hover {
  box-shadow: 0 4px 4px 0 var(--text-shadow-color);
}

.search-form--big[type="text"]:focus {
  text-shadow: 0 4px 4px var(--text-shadow-color);
  font-weight: bold;
}

.search-form--small {
  border: 1px solid var(--border-color);
  box-shadow: 0 1px 8px 0 var(--box-shadow-color);
  text-shadow: 0 4px 4px var(--text-shadow-color);
  background: url("Search.ccd3c07b.svg") 13px / 11px no-repeat;
  border-radius: 4px;
  height: 42px;
  margin-top: 20px;
  padding-left: 33px;
  line-height: 18px;
}

.search-form--small::placeholder {
  color: var(--main-text-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
}

.search-form--small:hover {
  box-shadow: 0 3px 8px 0 var(--box-shadow-color);
  text-shadow: none;
}

.search-form--small[type="text"]:focus {
  text-shadow: none;
  font-size: 14px;
  font-weight: bold;
  line-height: 19px;
}
